import React, { useEffect, useState } from 'react';
import { List } from 'react-virtualized';
import 'react-virtualized/styles.css';
// 
import InfiniteScroll from 'react-infinite-scroller';
import { LoadMoreButton } from './load_more_button_conpoment';

import ListsItem from './list_item';

const ProductsList = ({ productsData, loadMore, loading, hasMore, ItemRendererComponent, rowHeight }) => {
  const [screenWidth, setScreenWidth] = useState(500);
  useEffect(() => {
    console.log('set document');
    if (typeof document !== `undefined`) {
      setScreenWidth(document.body.clientWidth);
    }
  }, []);
  const rowRenderer = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    const currentItem = productsData[index];
    // console.log(productsData.length);
    return (
      <div key={key}>
        <ItemRendererComponent item={currentItem} index={index}></ItemRendererComponent>
      </div>)
  }

  const ListComponent = () => {
    // react-tiny-virtual-list
    let self_height = rowHeight * productsData.length;
    return (
      <List
        height={self_height}
        rowHeight={rowHeight}
        width={screenWidth}
        rowCount={productsData.length}
        rowRenderer={rowRenderer}
        style={{ padding: '0.1rem 0.2rem', margin: '0.2rem 0.4rem' }}></List>
    )
  };

  return (
    <div style={{ overflow: 'auto' }}>
      {/* <InfiniteScroll
        initialLoad={false}
        pageStart={0}
        loadMore={loadMore}
        hasMore={!loading && hasMore}
        useWindow={true}
      > */}
        <ListComponent></ListComponent>
        <LoadMoreButton hasMore={hasMore} onClick={loadMore} loading={loading}></LoadMoreButton>
        <div style={{ height: '50px', width: '100%', margin: '30px 0px' }}></div>
      {/* </InfiniteScroll> */}
    </div>
  )
}

ProductsList.defaultProps = {
  // productsData item={image_cos_name1: '', len_title: '', effects, '', len_price: '', len_sale_price: ''}
  productsData: [],
  loadMore: (startIndex, stopIndex) => {
    // 
    console.log('Start-index:', startIndex, '; stopIndex:', stopIndex);
  },
  loading: false,
  hasMore: false,
  ItemRendererComponent: ({ item, index }) => {
    //   
    // console.log(item);
    return (<div>
      <ListsItem
        listsIndex={index}
        url={item.image_cos_name1}
        productTitle={item.len_title}
        productEffects={item.effects}
        sourcePrice={item.len_price}
        salePrice={item.len_sale_price}
        clickDetailHandle={(listsIndex) => { console.log(listsIndex) }}
      ></ListsItem>
    </div>);
  },
  rowHeight: 240
}

export default ProductsList;
